ks-nocontent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    padding: 50px 30px 70px 30px;
    text-align: center;
}

ks-nocontent .content {
    max-width: 800px;
    margin: 15px 0;
}

ks-nocontent .content h1 {
    font-size: 1.275rem;
    font-family: var(--font-emphasis);
    font-weight: 700;
}

ks-nocontent .content h1 {
    font-size: 1.275rem;
}

ks-nocontent .buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

ks-nocontent .buttons > * {
    margin: 5px;
}

@media (max-width: 640px) {
    ks-nocontent {
        padding: 30px 15px 50px 15px;
    }
}