import { Component, h, Prop } from '@stencil/core';

@Component({
    tag: 'ks-nocontent',
    styleUrl: 'nocontent.css'
})
export class NotFound {
    @Prop() link: string = "/";
    @Prop() linkName: string;
    @Prop() backName: string;

	render() {
		return [
            <div class="content">
                <slot/>
            </div>,
            <div class="buttons">
                <ks-button round nofollow onClick={()=>history.back()} name={this.backName} />
                <ks-button round nofollow link={this.link} name={this.linkName} />
            </div>
        ];
	}
}
